
import { defineComponent, ref, onMounted } from "vue";
import Headbar from "@/components/headbar/index.vue";
import Lighting from "@/components/lighting/index.vue";
import { useRouter } from "vue-router";
import { Dialog } from "vant";
import nipplejs from "nipplejs";

enum Direction {
  Up = "up",
  Down = "down",
  Left = "left",
  Right = "right",
  Bottom = "bottom"
}

interface Game {
  ele: HTMLElement | null;
  maxLeft: number;
  maxBottom: number;
}

const game: Game = {
  ele: null,
  maxLeft: 0,
  maxBottom: 0
}

class Move {
  static jump(distance: number): void {
    this.updatePos("bottom", distance);
  }
  static down(distance: number):void {
    this.updatePos("bottom", -distance);
  }
  static forward(distance: number): void {
    this.updatePos("left", distance);
  }
  static backward(distance: number): void {
    this.updatePos("left", -distance);
  }
  static checkedExceed(direction: string, newPos: number): boolean {
    switch (direction) {
      case Direction.Left:
        if (newPos <= 0 || newPos >= game.maxLeft) return false;
        return true;
      case Direction.Bottom:
        if (newPos <= 0 || newPos >= game.maxBottom) return false;
        return true;
    }
    return false;
  }
  static updatePos(direction: string, currPos: number) {
    // 获取人物之前的位置
    let oldPos = parseInt(getComputedStyle(game.ele as HTMLElement).getPropertyValue(direction));
    let newPos = oldPos + currPos;
    // 判断是否溢出边界
    if (!this.checkedExceed(direction, newPos)) return;
    game!.ele!.style[direction as any] = newPos + "px";
  }
}

export default defineComponent({
  name: "CreateScene",
  components: {
    Headbar,
    Lighting,
  },
  setup() {
    const router = useRouter();
    let isShowLight = ref(true);
    let eyeStatus = ref(true); // true表示睁眼
    let roleInterMode = ref("wing");

    const clickEye = () => {
      eyeStatus.value = !eyeStatus.value;
    };

    const goHome = () => {
      Dialog.confirm({
        className: "confirm-scene-model",
        message: "确认返回主页？",
      })
        .then(() => {
          router.push({
            path: "/home",
          });
        })
        .catch(() => {});
    };

    const clickMode = (mode: string) => {
      if (mode == "wing") {
        roleInterMode.value = "land";
      }
      if (mode == "land") {
        roleInterMode.value = "wing";
      }
    };

    const clickLayoutBtn = () => {
      router.push("/layout-mode");
    };

    const clickCamera = () => {
      router.push("/camera");
      console.log("点击相机");
    };

    const onStart = (distance: number, angle: string) => {
      switch (angle) {
        case Direction.Up:
          Move.jump(distance);
          break;
        case Direction.Down:
          Move.down(distance);
          break;
        case Direction.Right:
          Move.forward(distance);
          break;
        default:
          Move.backward(distance);
          break;
      }
      return true;
    };

    const onEnd = () => {
      console.log("结束了");
    };

    onMounted(() => {
      game.ele = document.getElementById('avatar');
      let temp = <HTMLElement>document.querySelector(".create-scene-page");
      game.maxLeft = temp.offsetWidth - 50;
      game.maxBottom = temp.offsetHeight - 50;

      let distance: number = 0,
        angle: string = "",
        time: number = 0;
      const manager = nipplejs.create({
        zone: document.getElementById("dynamic") as HTMLElement,
        color: "white",
        mode: "dynamic",
        multitouch: true,
        shape: "circle",
      });
      manager.on("start", (evt, data) => {
        time = setInterval(() => {
          onStart && onStart(distance, angle);
        }, 100);
      });
      manager.on("move", (evt, data) => {
        if (data.direction) {
          // angle 表示上下左右
          angle = data.direction.angle;
          distance = data.distance;
        }
      });
      manager.on("end", (evt, data) => {
        clearInterval(time);
        onEnd && onEnd();
      });
    });

    return {
      isShowLight,
      eyeStatus,
      roleInterMode,
      clickEye,
      goHome,
      clickMode,
      clickLayoutBtn,
      clickCamera,
    };
  },
});
